.gallery {
  display: flex;
  flex-direction: column;
  margin-bottom: 150px;
}

.gallery__heading-mini {
  margin-top: 0px;
  margin-bottom: 15px;
  font-size: 21px;
  line-height: 140%;
  text-align: center;
  color: $accent-color;
}

.gallery__heading {
  margin-top: 0px;
  margin-bottom: 60px;
  font-family: 'Noto Serif', Georgia, serif;
  font-weight: bold;
  font-size: 30px;
  line-height: 41px;
  text-align: center;
  color: $accent-color;
}

.gallery__content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.gallery__video {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 555px;
  height: 300px;
}

.gallery__image {
  max-height: 100%;
}

.gallery__playbutton {
  position: absolute;
  fill: $white-color;
}

.gallery__playbutton:hover {
  transform: scale(1.07);
}

.gallery__gradient {
  position: absolute;
  background: linear-gradient(0deg, rgba(51, 51, 51, 0.4), rgba(51, 51, 51, 0.4));
  width: 555px;
  height: 300px;
}

.gallery__picture {
  background-color: $hard-gray-color;
  height: 300px;
}

@media (max-width: $mobile-width) {

  .gallery {
    margin-bottom: $section-mb-mobile;
  }

  .gallery__content {
    gap: 15px;
    align-items: center;
  }

  .gallery__video {
    width: 380px;
    height: 205px;
  }

  .gallery__gradient {
    width: 380px;
    height: 205px;
  }

  .gallery__image {
    width: 380px;
    height: 205px;
  }
}
